export const RE_ID = /^[-a-zA-Z\s]{2,30}$/ // ,  
export const RE_EID = /^[a-zA-Z\s]{2,30}$/ // ,    // 2017-03-07 
export const RE_EN = /[^a-zA-Z0-9(),-.?\s]+/ //     // 2017-07-03 
export const RE_TEL = /^[0-9]{1,3}-[0-9]{6,15}$/ //  
export const RE_TEL_ZERO = /^[0-9]{1,3}-[0-9]{8,16}$/ //  
export const RE_TEL_GLOBAL = /^[0-9]{1,6}-[0-9]{9,15}$/ //  
export const RE_POSTAL = /^[a-zA-Z0-9]{2,10}$/ //  
export const RE_MEMO = /[;<>&]/ //    
export const RE_COUNTRY = /^[0-9]{1,3}$/ //  3 // 2017-12-26 
export const RE_ADDRESS = /[^---a-zA-Z0-9(),&-.?\s]+/


